import { useState, useEffect, useRef } from 'react';
import {
  Box,
  Paper,
  Typography,
  TextField,
  IconButton,
  CircularProgress,
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';

import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';
import nutrienLogo from './image.png';


/*
Create new input box to take:
1. keyword
2. definition
3. example (optional - incident example in detail)

with a add more button.



Take dates after 2020/01/01 only   -> Done

Estimated time > 60 mins  -> Done
Max row 1000
*/

const questions = [
  "Hi! I'm Bobot - a SH&E AI Agent. How can I help you today?",
  "What kind of incidents would you like me to find? For example, 'Gas Monitor' incidents.",
  "Could you tell me what you mean by that? I'd like to understand it better.",
  'Which Business Unit should I focus on?',
  'What time frame or date range are you interested in?',
];

const businessUnitOptions = ['Retail', 'Nitrogen', 'Potash', 'Phosphate'];
const agentOptions = ['Find PowerBI', 'Analyze Incidents'];


function App() {
  const [messages, setMessages] = useState([
    {
      id: 1,
      text: questions[0],
      sender: 'bot',
      time: new Date().toLocaleTimeString(),
    },
  ]);
  const [input, setInput] = useState('');
  const [inputError, setInputError] = useState('');
  const [step, setStep] = useState(0);
  const [isTyping, setIsTyping] = useState(false);
  const [dateRange, setDateRange] = useState([null, null]);
  const [dateError, setDateError] = useState('');
  const [answers, setAnswers] = useState({});

  const messagesEndRef = useRef(null);
  const chatScrollRef = useRef(null);
  const messagesContainerRef = useRef(null);


  const sendDataToApi = async (answers) => {
    const dateRange = answers[questions[3]]; // "YYYY-MM-DD to YYYY-MM-DD"
    const [start_date, end_date] = dateRange.split(' to ');

    const payload = {
      incidents: answers[questions[0]],
      definition: answers[questions[1]],
      business_unit: answers[questions[2]],
      start_date,
      end_date,
    };

    try {
      const response = await fetch(
        'http://127.0.0.1:8000/api/v1/incident_data',
        {
          method: 'POST',
          headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        }
      );

      if (!response.ok) {
        throw new Error(`API error: ${response.status}`);
      }

      const data = await response.json();
      console.log('API response:', data);
      return data;
    } catch (error) {
      console.error('Failed to send data:', error);
      throw error;
    }
  };
  
//   useEffect(() => {
//   const el = chatScrollRef.current;
//   if (!el) return;

//   const onWheel = (e) => {
//     const { scrollTop, scrollHeight, clientHeight } = el;
//     const atTop = scrollTop === 0;
//     const atBottom = scrollTop + clientHeight >= scrollHeight - 1;

//     if (
//       (e.deltaY < 0 && atTop) ||
//       (e.deltaY > 0 && atBottom)
//     ) {
//       e.preventDefault(); 
//     }
//   };

//   el.addEventListener('wheel', onWheel, { passive: false });
//   return () => el.removeEventListener('wheel', onWheel);
// }, []);


 useEffect(() => {
  if (messagesContainerRef.current) {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }
}, [messages, isTyping]);

  useEffect(() => {
    if (Object.keys(answers).length === questions.length) {
      setIsTyping(true);

      setTimeout(async () => {
        try {
          await sendDataToApi(answers);

          const finalMsg = {
            id: Date.now() + 1000,
            text: 'Thank you for providing all the details! The data has been sent for processing, and you can expect the file by email in under 60 minutes.',
            sender: 'bot',
            time: new Date().toLocaleTimeString(),
          };

          setMessages((prev) => [...prev, finalMsg]);
        } catch {
          setMessages((prev) => [
            ...prev,
            {
              id: Date.now() + 2000,
              text: 'Thank you for providing all the details! The data has been sent for processing, and you can expect the file by email in under 60 minutes.',
              sender: 'bot',
              time: new Date().toLocaleTimeString(),
            },
          ]);
        } finally {
          setIsTyping(false);
        }
      }, 1500);
    }
  }, [answers]);

  const sendUserMessage = (text) => {
    const userMsg = {
      id: Date.now(),
      text,
      sender: 'user',
      time: new Date().toLocaleTimeString(),
    };

    setMessages((prev) => [...prev, userMsg]);

    const prevBotQuestion = messages[messages.length - 1]?.text;
    if (prevBotQuestion && messages[messages.length - 1]?.sender === 'bot') {
      setAnswers((prev) => ({
        ...prev,
        [prevBotQuestion]: text,
      }));
    }

    if (step < questions.length - 1) {
      setIsTyping(true);
      setTimeout(() => {
        const nextStep = step + 1;
        setMessages((prev) => [
          ...prev,
          {
            id: Date.now() + 1,
            text: questions[nextStep],
            sender: 'bot',
            time: new Date().toLocaleTimeString(),
          },
        ]);
        setStep(nextStep);
        setIsTyping(false);
      }, 1500);
    }
  };

  const handleSend = () => {
    if (!input.trim()) return;

    if (input.trim().length < 8) {
      setInputError('Message must be at least 8 characters.');
      return;
    }

    setInputError('');
    sendUserMessage(input);
    setInput('');
  };

  const handleOptionClick = (option) => {
    if (isTyping) return;
    sendUserMessage(option);
  };

  const handleDateConfirm = () => {
    if (!dateRange[0] || !dateRange[1]) return;

    if (dayjs(dateRange[0]).isAfter(dateRange[1])) {
      setDateError('Start date cannot be after end date');
      return;
    }

    if (dayjs(dateRange[0]).isBefore('2020/01/01') || dateRange[1].isBefore('2020/01/01')) {
      setDateError('Start and end dates should be after 2020/01/01');
      return;
    }


    const formattedRange = `${dayjs(dateRange[0]).format(
      'YYYY-MM-DD'
    )} to ${dayjs(dateRange[1]).format('YYYY-MM-DD')}`;

    sendUserMessage(formattedRange);
    setDateRange([null, null]);
    setDateError('');
  };

  const allAnswered = Object.keys(answers).length === questions.length;

  return (
    <Box
      
      onWheel={(e) => e.stopPropagation()} // prevent wheel bubbling to parent
      ref={messagesContainerRef}
      sx={{
        height: '100vh',
        display: 'flex',
        maxHeight: '100%',
        justifyContent: 'center',
        alignItems: 'center',
        bgcolor: '#ebecec',
        overflow: 'auto',        //  stops scroll bleed
        overscrollBehavior: 'contain', //  KEY LINE
      }}
    >
      <Paper
        sx={{
          width: '100%',
          height: '100%',
          bgcolor: '#fcfcfc',
          display: 'flex',
          flexDirection: 'column',
          borderRadius: 3,
        }}
      >
        {/* Header */}
        <Box
          sx={{
            p: 2,
            borderBottom: '1px solid #1e293b',
            bgcolor: '#3B6500',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
          }}
        >
          <Box
            component='img'
            src={nutrienLogo}
            sx={{ height: 40, width: 35 }}
          />
          <Typography variant='h6' sx={{ color: '#fff' }}>
            SHE Data and Analytics
          </Typography>
        </Box>

        {/* Messages */}
        <Box sx={{ flex: 1, p: 2, overflowY: 'auto' }}>
          {messages.map((msg) => (
            <Box
              key={msg.id}
              sx={{
                display: 'flex',
                justifyContent:
                  msg.sender === 'user' ? 'flex-end' : 'flex-start',
                mb: 2,
              }}
            >
              <Box
                sx={{
                  maxWidth: '75%',
                  p: 1.5,
                  borderRadius: 2,
                  bgcolor: '#3B6500',
                }}
              >
                <Typography variant='body2' sx={{ color: '#fff' }}>
                  {msg.text}
                </Typography>
                <Typography
                  variant='caption'
                  sx={{
                    opacity: 0.6,
                    display: 'block',
                    mt: 0.5,
                    color: '#fff',
                  }}
                >
                  {msg.time}
                </Typography>
              </Box>
            </Box>
          ))}

          {isTyping && (
            <Box sx={{ display: 'flex', mb: 2 }}>
              <Box
                sx={{
                  p: 1.5,
                  borderRadius: 2,
                  bgcolor: '#3B6500',
                  display: 'flex',
                  alignItems: 'center',
                  gap: 1,
                }}
              >
                <CircularProgress size={20} sx={{ color: 'white' }} />
                <Typography variant='body2' sx={{ color: '#fff' }}>
                  typing...
                </Typography>
              </Box>
            </Box>
          )}

          <div ref={messagesEndRef} />
        </Box>

        {!allAnswered && (
          <Box
            sx={{
              p: 2,
              borderTop: '1px solid #2c4258',
              bgcolor: '#3B6500',
            }}
          >
            {
            step === 0 ? (
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  width: '100%',
                  pointerEvents: isTyping ? 'none' : 'auto',
                  opacity: isTyping ? 0.6 : 1,
                }}
              >
                {agentOptions.map((option) => (
                  <Box
                    key={option}
                    sx={{
                      bgcolor: '#fff',
                      color: '000',
                      px: 2,
                      py: 1,
                      borderRadius: 1,
                      cursor: isTyping ? 'not-allowed' : 'pointer',
                      userSelect: 'none',
                      flex: 1,
                      textAlign: 'center',
                      mx: 0.5,
                      '&:hover': { bgcolor: isTyping ? '#3b3b3d' : '#fff' },
                    }}
                    onClick={() => handleOptionClick(option)}
                  >
                    {option}
                  </Box>
                ))}
              </Box>
            ) :
            
            step === 3 ? (
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  width: '100%',
                  pointerEvents: isTyping ? 'none' : 'auto',
                  opacity: isTyping ? 0.6 : 1,
                }}
              >
                {businessUnitOptions.map((option) => (
                  <Box
                    key={option}
                    sx={{
                      bgcolor: '#fff',
                      color: '000',
                      px: 2,
                      py: 1,
                      borderRadius: 1,
                      cursor: isTyping ? 'not-allowed' : 'pointer',
                      userSelect: 'none',
                      flex: 1,
                      textAlign: 'center',
                      mx: 0.5,
                      '&:hover': { bgcolor: isTyping ? '#3b3b3d' : '#fff' },
                    }}
                    onClick={() => handleOptionClick(option)}
                  >
                    {option}
                  </Box>
                ))}
              </Box>
            ) : step === 4 ? (
              <LocalizationProvider dateAdapter={AdapterDayjs}>
                <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
                  <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                    <DatePicker
                      label='Start Date'
                      disableFuture
                      value={dateRange[0]}
                      onChange={(newValue) => {
                        if (
                          dateRange[1] &&
                          dayjs(newValue).isAfter(dateRange[1])
                        ) {
                          setDateError('Start date cannot be after end date');
                        } else if (dayjs(newValue).isBefore('2020/01/01')) {
                          setDateError('Start date should be after 2020/01/01');
                        } else {
                          setDateError('');
                        }
                        setDateRange([newValue, dateRange[1]]);
                      }}
                      slotProps={{
                        textField: {
                          size: 'small',
                          sx: {
                            bgcolor: '#f2f2fc',
                            borderRadius: 1,
                            '& input': { color: 'black !important' },
                            '& .MuiFormLabel-root': { color: 'black' },
                            '& .MuiSvgIcon-root': { color: 'black' },
                          },
                        },
                      }}
                    />
                    <DatePicker
                      label='End Date'
                      disableFuture
                      value={dateRange[1]}
                      onChange={(newValue) => {
                        if (
                          dateRange[0] &&
                          dayjs(newValue).isBefore(dateRange[0])
                        ) {
                          setDateError('End date cannot be before start date');
                        } else if (dayjs(newValue).isBefore('2020/01/01')) {
                          setDateError('End date should be after 2020/01/01');
                        }else {
                          setDateError('');
                        }
                        setDateRange([dateRange[0], newValue]);
                      }}
                      slotProps={{
                        textField: {
                          size: 'small',
                          sx: {
                            bgcolor: '#f2f2fc',
                            borderRadius: 1,
                            '& input': { color: 'black !important' },
                            '& .MuiFormLabel-root': { color: 'black' },
                            '& .MuiSvgIcon-root': { color: 'black' },
                          },
                        },
                      }}
                    />
                    <IconButton
                      onClick={handleDateConfirm}
                      disabled={
                        !dateRange[0] ||
                        !dateRange[1] ||
                        !!dateError ||
                        isTyping
                      }
                      sx={{ color: '#fff' }}
                    >
                      <SendIcon />
                    </IconButton>
                  </Box>

                  {dateError && (
                    <Typography variant='caption' sx={{ color: '#ffb4b4' }}>
                      {dateError}
                    </Typography>
                  )}
                </Box>
              </LocalizationProvider>
            ) : (
              <Box sx={{ display: 'flex', gap: 1 }}>
                <TextField
                  fullWidth
                  size='small'
                  value={input}
                  placeholder='Type your message...'
                  onChange={(e) => setInput(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      handleSend();
                    }
                  }}
                  error={!!inputError}
                  helperText={inputError}
                  disabled={step >= questions.length || isTyping}
                  sx={{ bgcolor: '#fff', borderRadius: 1 }}
                />
                <IconButton
                  onClick={handleSend}
                  disabled={input.trim().length < 8 || isTyping}
                  sx={{ color: '#fff' }}
                >
                  <SendIcon />
                </IconButton>
              </Box>
            )}
          </Box>
        )}
      </Paper>
    </Box>

  );
}

export default App;
